import { ReactElement } from "react";

export interface R114MapProps {
  ncpClientId?: string;
}

export type MapReducerState = {
  map?: naver.maps.Map;
  /**
   * ### 작동원리
   * Map에 객체 요소 `id`와 같은 값으로 마커 영역을 잡아주고
   * div#id 엘리멘트를 컨테이너로 component를 React.createPortal을 활용하여 렌더 합니다.
   *
   * ### 유의사항
   * 따라서 setMarker는 영역만 잡아주는 역할만 하며,
   * 컴포넌트 렌더는 개발자가 직접 수행해야 합니다.
   *
   * @example
   * ```tsx
   * const Component = () => {
   *  return (
   *    {markers.map((marker) => {
   *      const container = document.getElementById(marker.id);
   *      if (container) {
   *        return createPortal(marker.component, container);
   *      } else {
   *        return null;
   *      }
   *    })
   *  })
   * }
   * ```
   *
   */
  markers: { id: string; marker: naver.maps.Marker; component: ReactElement }[];
};

export type MapReducerAction = MapReducerActionConfig[MapReducerActionType];

type MapReducerActionType = keyof MapReducerActionPayload;

type MapReducerActionConfig = {
  [key in keyof MapReducerActionPayload]: {
    type: key;
    payload: MapReducerActionPayload[key];
  };
};

interface MapReducerActionPayload {
  initMap: naver.maps.Map;
  setMarker: {
    id: string;
    position: naver.maps.LatLng;
    component: ReactElement;
  };
  clearMarker: undefined;
}

export enum SaleType {
  CODE,
  NONE_CODE,
}

export interface CodeData {
  id: string;
  name: string;
  coord: [number, number];
  price: number;
}
